.nav {
    background-color: #333333;
    overflow: hidden;
}

.nav a {
    float: left;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    font-size: 17px;
}

.nav a:hover {
    background-color: #dddddd;
    color: black;
}

.nav a.current {
    background-color: #4CAF50;
    color: white;
}

.images {
    display: grid;
    grid-template-columns: 200px auto;
    grid-gap: 10px;
}

hr {
    border: 0;
    height: 1px;
    background-image: linear-gradient(to right, rgba(0,0,0,0), rgba(0,0,0,1), rgba(0, 0, 0, 0));
}

#content {
    margin-left: 15%;
    margin-right:15%;
    font-size: x-large;
}

#projectTitle {
    text-align: center;
    font-size: xxx-large;
}

#title {
    font-size: xx-large;
    text-align: center;
}

#portfolioLink {
    color: #000000;
    text-decoration: none;
}

html, body { 
    margin:0;
    padding:0;
    width:100%;
    height:100%;
    overflow: auto;
}

iframe {
    vertical-align: middle;
    display: block;
    margin-left:auto;
    margin-right:auto;
}

#portfolioLink:hover h2 {
    color: gray;
}